.heed-ejet-section {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: #ffffff;
}

/* Make the WebGL canvas truly fill the viewport */
.heed-ejet-section canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;        /* full viewport width  */
  height: 100vh;       /* full viewport height */
  object-fit: cover;   /* scale & crop so no empty bars */
  image-rendering: auto;
  backface-visibility: hidden;
  transform: translateZ(0);
}

/* Loading overlay stays centered */
.loading-message {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #000;
  font-family: sans-serif;
  text-align: center;
  z-index: 100;
  font-size: 1rem;
  padding: 0 10px;
}

/* Spinner */
.spinner {
  width: 80px;
  height: 80px;
  border: 3px solid rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  border-top-color: #000;
  animation: spin 1s linear infinite;
  margin: 0 auto 1rem;
}

/* Animation keyframes */
@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Responsive tweaks */
@media (max-width: 768px) {
  .loading-message { font-size: 0.9rem; }
  .spinner {
    width: 50px;
    height: 50px;
    border-width: 2px;
  }
}

@media (min-width: 769px) {
  .loading-message { font-size: 1.2rem; }
  .spinner {
    width: 100px;      /* fixed the missing value */
    height: 100px;     /* match width for a perfect circle */
    border-width: 4px;
  }
}
