/* ───────────────────────────────────────────── */
/* Desktop view – default (≥ 1025 px)           */
/* ───────────────────────────────────────────── */
html, body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  background: #000;
}

/* Full-screen hero section */
.heed-jet-section {
  position: relative;
  width: 100vw;   /* full width of viewport */
  height: 100vh;  /* full height of viewport */
  overflow: hidden;
  background: #000;
}

/* Canvas always stretches to section size */
.heed-jet-section canvas {
  display: block;
  width: 100%;
  height: 100%;
  background: #000;
  border: none;
  transform: translateZ(0) scale(1);     /* base scale */
}

/* Loading overlay */
.loading-message {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #dcdcdc;
  font-family: sans-serif;
  font-size: 1rem;
  z-index: 100;
  text-align: center;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 3px solid #3c3c3c;
  border-radius: 50%;
  border-top-color: #fff;
  animation: spin 1s linear infinite;
  margin: 0 auto 1rem;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* ───────────────────────────────────────────── */
/* Tablet view (769 px – 1024 px)                */
/* ───────────────────────────────────────────── */
@media (min-width: 769px) and (max-width: 1024px) {
  .heed-jet-section canvas {
    transform: translateZ(0) scale(1.1); /* slight zoom */
  }
  .loading-message { font-size: 1.2rem; }
  .spinner {
    width: 50px;
    height: 50px;
    border-width: 4px;
  }
}

/* Responsive tweaks */
@media (max-width: 768px) {
  .loading-message { font-size: 0.9rem; }
  .spinner {
    width: 50px;
    height: 50px;
    border-width: 2px;
  }
}

@media (min-width: 769px) {
  .loading-message { font-size: 1.2rem; }
  .spinner {
    width: 100px;      /* fixed the missing value */
    height: 100px;     /* match width for a perfect circle */
    border-width: 4px;
  }
}

