/* ────────────────────────────────────────────────────────── */
/* Footer Styles – Desktop Base (≥1025px)                    */
/* ────────────────────────────────────────────────────────── */
.footer {
  background: #0f0f0fd6;
  color: #ede4e4;
  width: 100%;
  padding: 40px 20px;
  font-family: "Agency FB", sans-serif;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  justify-content: space-between;
}

.footer-section {
  flex: 1 1 250px;
  min-width: 240px;
}

.footer-section h4 {
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 600;
}

/* Contact & newsletter text */
.footer-section p,
.footer-section ul li,
.footer-section a {
  font-size: 18px;
  line-height: 1.6;
  color: #ccc;
  text-decoration: none;
}

.footer-section a:hover {
  color: #fff;
  text-decoration: underline;
}

/* Social Icons with floating animation */
@keyframes float {
  0%, 100% { transform: translateY(0); }
  50%      { transform: translateY(-8px); }
}

.social-icons {
  display: flex;
  gap: 1.2rem;
  align-items: center;
  margin-top: 0.5rem;
  flex-wrap: wrap;
}

.social-icons a {
  color: #ccc;
  transition: color 0.2s;
  animation: float 3s ease-in-out infinite;
}

.social-icons a:nth-child(1) { animation-duration: 2.8s; animation-delay: 0s; }
.social-icons a:nth-child(2) { animation-duration: 3.2s; animation-delay: 0.4s; }
.social-icons a:nth-child(3) { animation-duration: 2.6s; animation-delay: 0.8s; }

.social-icons a:hover {
  color: #fff;
}

/* Newsletter form */
input[type="email"] {
  padding: 10px;
  width: 70%;
  border: none;
  border-radius: 4px;
  margin-right: 8px;
  font-size: 16px;
  max-width: 300px;
}

button {
  margin-top: 10px;
  background-color: #73e7edbe;
  border: none;
  border-radius: 4px;
  color: #000;
  font-weight: bold;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background-color: #6be2e9;
}

/* Bottom line */
.footer-bottom {
  text-align: center;
  margin-top: 40px;
  border-top: 1px solid #444;
  padding-top: 20px;
  font-size: 18px;
  color: #888;
}



/* ────────────────────────────────────────────────────────── */
/* Tablet View (769px – 1024px)                              */
/* ────────────────────────────────────────────────────────── */
@media (min-width: 769px) and (max-width: 1024px) {
  .footer-container {
    gap: 32px;
    justify-content: space-around;
  }

  .footer-section {
    flex: 1 1 45%;
    min-width: unset;
  }

  .footer-section h4 {
    font-size: 17px;
  }

  .footer-section p,
  .footer-section ul li,
  .footer-section a {
    font-size: 17px;
  }

  .social-icons {
    gap: 1rem;
  }

  input[type="email"] {
    width: 80%;
    max-width: none;
  }

  button {
    width: auto;
    padding: 10px 20px;
  }

  .footer-bottom {
    font-size: 17px;
    padding-top: 18px;
  }
}



/* ────────────────────────────────────────────────────────── */
/* Mobile View (≤768px)                                      */
/* ────────────────────────────────────────────────────────── */
@media (max-width: 768px) {
  .footer-container {
    gap: 24px;
    flex-direction: column;
    align-items: flex-start;
  }

  .footer-section {
    flex: 1 1 100%;
    min-width: unset;
  }

  .footer-section h4 {
    font-size: 16px;
  }

  .footer-section p,
  .footer-section ul li,
  .footer-section a {
    font-size: 16px;
    line-height: 1.4;
  }

  .social-icons {
    justify-content: flex-start;
    gap: 1rem;
  }

  input[type="email"] {
    width: 100%;
    margin-bottom: 15px;
  }

  button {
    width: 40%;
  }

  .footer-bottom {
    font-size: 16px;
  }
}
