/* ───────────────────────────────────────────── */
/* Header Styles – Desktop Base (≥1025px)       */
/* ───────────────────────────────────────────── */
.heed-header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;

  width: 100%;
  height: 60px;

  background: #f9f9f9aa;
  border: 2px solid #f6f8f8c8;
  border-radius: 10px;
  box-shadow: 0 1px 4px rgba(248, 244, 244, 0.05);

  margin: 2px auto 0;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;

  padding: 0 20px;
}

/* Logo Styling */
.heed-logo {
  height: 240px;
  object-fit: contain;
  max-width: 100%;
}


/* ───────────────────────────────────────────── */
/* Tablet Styles (769px – 1024px)               */
/* ───────────────────────────────────────────── */
@media (min-width: 769px) and (max-width: 1024px) {
  .heed-header {
    height: 60px;            /* keep header height consistent */
    border-radius: 8px;      /* slightly smaller corners */
    padding: 0 15px;
  }

  .heed-logo {
    height: 200px;           /* scaled-down logo */
  }
}


/* ───────────────────────────────────────────── */
/* Mobile Styles (≤768px)                       */
/* ───────────────────────────────────────────── */
@media (max-width: 768px) {
  .heed-header {
    height: 60px;
    border-radius: 0;
    padding: 0 10px;
  }

  .heed-logo {
    height: 170px;
  }
}
