/* ───────────────────────────────────────────── */
/* ➊ Hero wrapper (Desktop default ≥1025px)      */
/* ───────────────────────────────────────────── */
.hero-banner {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  font-family: 'AGENCYFB', sans-serif;
}

/* ➋ Video always behind everything */
.video-banner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1;
  overflow: hidden;
  background-color: #000;
}
.video-banner .background-video {
  width: 100vw;
  height: 100vh;
  object-fit: fill;
  object-position: center;
  display: block;
  background-color: #000;
}

/* ➌ Loader overlay (full-screen) */
.loader-overlay,
.loading-screen {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 1.5rem;
  z-index: 10;
}

/* ➍ Text overlay at bottom */
.banner-overlay {
  position: absolute;
  bottom: 150px;
  width: 100%;
  text-align: center;
  color: #fff;
  z-index: 5;
  text-shadow: 2px 2px 6px rgba(0, 0, 0, 0.6);
  padding: 0 10px;
}

/* ➎ Buttons container (pushed down) */
.banner-buttons {
  margin-top: 5rem;    /* increased from 3rem to 5rem */
  display: flex;
  gap: 3rem;
  justify-content: center;
  flex-wrap: wrap;
}

/* ➏ CTA button styles */
.cta-button {
  width: 500px;
  height: 120px;
  background-color: rgba(65, 204, 235, 0.975);
  color: #fcfcfc;
  font-size: 2rem;
  font-weight: bold;
  border: none;
  border-radius: 20px;
  box-shadow: -6px 6px 12px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
  font-family: 'AGENCYFB', sans-serif;
}
.cta-button:hover {
  transform: translateY(-4px);
  box-shadow: -8px 8px 16px rgba(34, 82, 85, 0.25);
}

/* ➐ Header styles */
.heed-header {
  width: 100%;
  background: #dbecf2;
  border: 2px solid #141515;
  border-radius: 10px;
  margin: 2px auto 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  box-sizing: border-box;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}
.heed-logo {
  height: 150px;
  object-fit: contain;
}

/* ➑ Scroll-down arrow */
.scroll-down {
  margin-top: 2rem;
  background: transparent;
  border: none;
  font-size: 2rem;
  color: #fff;
  cursor: pointer;
  animation: bounce 2s infinite;
}
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(5px); }
  60% { transform: translateY(3px); }
}

/* Tablet View (769px – 1024px) */
@media (min-width: 769px) and (max-width: 1024px) {
  .banner-overlay {
    bottom: 120px;
    font-size: 1.5rem;
    padding: 0 20px;
  }
  .banner-buttons {
    margin-top: 4rem; /* adjust if needed on tablet */
    gap: 1.5rem;
  }
  .cta-button {
    width: 350px;
    height: 90px;
    font-size: 1.5rem;
    border-radius: 16px;
  }
  .heed-header {
    height: 70px;
    border-radius: 8px;
    margin: 4px auto 6px;
  }
  .heed-logo {
    height: 130px;
  }
  .scroll-down {
    font-size: 1.8rem;
    margin-top: 1.5rem;
  }
  .loader-overlay,
  .loading-screen {
    font-size: 1.3rem;
    padding: 0 30px;
  }
}

/* Mobile View (≤768px) */
@media (max-width: 768px) {
  .banner-overlay {
    bottom: 80px;
    font-size: 1.2rem;
    padding: 0 15px;
  }
  .banner-buttons {
    margin-top: 3rem; /* adjust if needed on mobile */
    gap: 1rem;
  }
  .cta-button {
    width: 80%;
    height: 60px;
    font-size: 1.2rem;
    border-radius: 12px;
  }
  .heed-header {
    height: 60px;
    border-radius: 0;
    margin: 0;
  }
  .heed-logo {
    height: 120px;
  }
  .scroll-down {
    font-size: 1.4rem;
    margin-top: 1rem;
  }
  .loader-overlay,
  .loading-screen {
    font-size: 1.1rem;
    padding: 0 20px;
    text-align: center;
  }
}
